import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { DemoLazyChildComponent } from './demo-lazy-child.component';

const routes: Routes = [
  {
    path: '',
    component: DemoLazyChildComponent
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class DemoLazyChildRoutingModule { }
